    justify-content: space-between; }
  .header .contact {
    display: flex;
    align-items: center; }
    .header .contact__mail {
      margin: 0 85px 0 0; }
      @media (max-width: 1023px) {
        .header .contact__mail {
          margin: 0 30px 0 0; } }
    .header .contact__phone {
      margin: 0 50px 0 0; }
      @media (max-width: 1023px) {
        .header .contact__phone {
          margin: 0; } }
    .header .contact__mail, .header .contact__phone {
